import React, { useEffect } from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { Cookies } from 'react-cookie'
import { Avatar, Chip } from '@material-ui/core';

export default function FormDialog() {
    const cookies = new Cookies()
    const [open, setOpen] = React.useState(false);
    const [mail, setMail] = React.useState(cookies.get('email'));

    const handleClose = () => {
        setOpen(false);
    };

    function validerEmail() {
        cookies.set("email", mail, { path: '/' })
        setOpen(false)
    }

    useEffect(() => {
        if (!cookies.get('email')) {
            setTimeout(() => {
                setOpen(true)
            }, 3000);
        }
    }, [])

    function inputMail({ target: { value } }) {
        setMail(value)
    }

    return (
        <div>

            {
                mail ? <span><Chip className="bg-light" avatar={<Avatar>{mail[0]}</Avatar>} label={mail} onClick={() => setOpen(true)} /> confguré pour recevoir les liens</span> : <span>
                    <Chip className="bg-light" label={"Configuré un mail"} onClick={() => setOpen(true)} />
                </span>
            }

            <Dialog
                open={open}
                onClose={handleClose}
                disableBackdropClick
                aria-labelledby="form-dialog-title">
                <DialogTitle id="form-dialog-title">Confidentialité</DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Parce que votre sécurité compte pour nous, veuillez renseignez votre mail afin de recevoir en toute confidentialité les liens de vos fichiers traités
                    </DialogContentText>
                    <TextField
                        autoFocus
                        margin="dense"
                        id="name"
                        label="Email"
                        type="email"
                        fullWidth
                        defaultValue={mail}
                        onChange={inputMail}
                    />
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleClose} >Retour</Button>
                    <Button disabled={!(/^[a-z0-9]+@[a-z]+\.(fr|com|ci|yahoo|co)$/.test(mail))} onClick={validerEmail} className="bg-light">Valider</Button>
                </DialogActions>
            </Dialog>
        </div>
    );
}
