import React, { useState } from 'react'
import { Button } from '@material-ui/core'
import CloudUploadIcon from '@material-ui/icons/CloudUpload';
import { useDropzone } from 'react-dropzone';
import Axios from 'axios';
import { header } from './apiQuery';

const App = () => {
    const { getRootProps, getInputProps } = useDropzone()
    const [listFile, setlistFile] = useState([])
    const [loading, setloading] = useState(false)
    const [percent, setpercent] = useState(null)

    function sendFiles(files) {
        setloading(true)
        Axios({
            onUploadProgress: function (progressEvent) {
                let percentCompleted = Math.round((progressEvent.loaded * 100) / progressEvent.total)
                setpercent(percentCompleted)
            },
            method: 'POST',
            url: `${header.url}/upload/`,
            data: files,
            headers: { "content-type": "multipart/form-data", "x-filename": files }
        }).then((data) => {
            console.log("fichiers envoyés");
        })
    }
    return (
        <div className="row d-flex flex-column">
            <div>
                <Button {...getRootProps()} className="bg-light mt-4" startIcon={<CloudUploadIcon />} variant="contained" size="large">Importer votre fichier (.csv, .vcf)
            <input
                        {...getInputProps()}
                        className="col-12"
                        accept=".csv, .vcf, .zip"
                        type="file"
                        name='fichier'
                        multiple
                        onChange={({ target: { files } }) => {
                            const data = new FormData()
                            for (let i = 0; i < files.length; i++) {
                                data.append('fichier', files[i]);
                            }
                            sendFiles(data)
                        }}
                    />
                </Button>
            </div>
            <div className="row d-flex">
                <small>Vous pouvez nous écrire au pour la prise en charge des fichiers spécifiques à votre entreprise.</small>
            </div>
        </div>

    )
}

export default App